@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
body {
    font-family: 'Montserrat', sans-serif;
    /* font-size: 16px; */
}
header {
    background-color: #711955;
    color: white;
    border-bottom: 5px solid #f2b825;
    padding: 25px;
}
.form-control {
    font-size: 16px;
    }
thead th span{
    cursor: pointer;
}

tbody {
    display: block;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 65vh;
}

thead,
tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.email {
    overflow-x: auto;
}
